.like {
  margin-top: 10px;
}

.like__label {
  color: rgb(145, 145, 145);
  margin-right: 15px;
}

.like__wrapper {
  display: flex;
  flex-direction: row;
}

.like__img {
  width: 21px;
  height: 21px;
  background-repeat: no-repeat;
  background-position: center;
  object-fit: contain;
  content: url(../../UI/Images/like.png);
  margin: 0;
}
